.nav-link {
  color: var(--second-text-color);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16667;
  transition: color .5s;
}

.nav-link:hover, .nav-link:focus {
  color: var(--red-color);
}

.nav-link.current {
  transition: color .7s;
  position: relative;
}

.nav-link.current:after {
  content: attr(data-text);
  width: 100%;
  height: 3px;
  background-color: var(--red-color);
  transition: transform .5s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(4px);
}

.nav-link.current:hover, .nav-link.current:focus {
  color: var(--red-color);
}

.nav-link.current:hover:after, .nav-link.current:focus:after {
  transform: translateY(-17px);
}

.nav-btn {
  color: var(--second-text-color);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  font-size: 9px;
  display: flex;
  position: relative;
}

.nav-btn:before {
  content: "";
  width: 18px;
  height: 18px;
  background-image: url("user.9946bdde.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.ellipse {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -45%);
}

.ellipse > path {
  stroke-dashoffset: 147.58px;
  stroke-dasharray: 147.58;
  transition: stroke-dashoffset .6s ease-in-out;
}

.nav-btn:hover path {
  stroke-dashoffset: 0;
}

